import React from 'react';
import PropTypes from 'prop-types';
import NavigationList from './navigation-list';
import MastheadLinks from './masthead-links';
import 'reset-css/reset.css';
import '../grid/grid';
import '../media-queries/media-queries';
import { urls as defaultUrls, urlsPropShape } from '../../constants';
import css from './masthead.css';

const MastheadNavigation = ({ menuIsOpen, urls, showNavToggle }) => {
  return (
    <nav className={css['ds-masthead-nav-lists']}>
      {/* Allows for tabbing from the menu disclosure into the navigation list. */}
      <div id='menu-nav' tabIndex='-1' role='presentation' />
      <ul>
        {menuIsOpen && <MastheadLinks inverse />}
        <NavigationList
          type='feature'
          baseUrl={urls.economist}
          menuIsOpen={menuIsOpen}
          showNavToggle={showNavToggle}
          listIsOpen
        />
        <NavigationList
          type='section'
          baseUrl={urls.economist}
          menuIsOpen={menuIsOpen}
          showNavToggle={showNavToggle}
        />
        <NavigationList
          type='more'
          baseUrl={urls.economist}
          menuIsOpen={menuIsOpen}
          showNavToggle={showNavToggle}
        />
      </ul>
    </nav>
  );
};

MastheadNavigation.defaultProps = {
  urls: defaultUrls,
};

MastheadNavigation.propTypes = {
  menuIsOpen: PropTypes.bool.isRequired,
  urls: urlsPropShape,
  showNavToggle: PropTypes.bool.isRequired,
};

export default MastheadNavigation;
